import * as React from 'react';
import { Controller, environment } from '@ali/4ever-cangjie';
import { EraserButton } from '@ali/we-toolbar';
import { PluginRoles } from '@ali/4ever-bamboo';
import { clearStyles } from '../actions';

const MOD = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';

export const CLEAR_STYLE_HOT_KEY = `${MOD}+\\`;

export interface ClearStyleButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

export default function ClearStyleButton(props: ClearStyleButtonProps) {
  const { controller, locale, ...rest } = props;
  const handleClick = React.useCallback(() => {
    controller.run('onAction', clearStyles());
  }, [controller]);

  return (
    <EraserButton
      {...rest}
      onClick={handleClick}
      shortcutText={CLEAR_STYLE_HOT_KEY}
      testid={'bi-toolbar-clearstyle'}
      role={PluginRoles.clear}
    />
  );
}
